import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
// import { withRouter } from 'react-router-dom';
import LoginForm from '../components/Login/LoginForm';
import Logo from '../components/Logo/Logo';
import {
  changeLoginForm,
  getGeolocation,
  successUserAuthentication
} from '../actions';
import { postLogin } from '../api';
import { objectKeysToCamelCase } from '../utility/formattingData';
import { Auth } from 'aws-amplify';
import awsconfig from '../aws-config';
import { Amplify } from 'aws-amplify';

Amplify.configure(awsconfig);

const API_URL = 'https://jxsbyfmks7.execute-api.ap-northeast-2.amazonaws.com/prod';

const Login = () => {
  const [error, setError] = useState('');
  const login = useSelector(state => state.login);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleLoginSuccess = () => {
    navigate('/profile'); //  v6 
  };

  useEffect(() => {
    const getPosition = () => {
      return new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject);
      });
      // <button onClick={handleLoginSuccess}></button>);
    };

    getPosition()
      .then(position => {
        console.log(position.coords);
        dispatch(
          getGeolocation('login', {
            latitude: position.coords.latitude,
            longitude: position.coords.longitude
          })
        );
      })
      .catch(() => {
        setError('    .');
      });
  }, [dispatch]);

  const onChange = ev => {
    const { value, name } = ev.target;
    dispatch(
      changeLoginForm({
        name,
        value
      })
    );
  };

  const onSubmit = async ev => {
    ev.preventDefault();
    try {
      //  Cognito 
      const user = await Auth.signIn(login.email, login.password);
      const idToken = user.signInUserSession.idToken.jwtToken;
  
      //  JWT 
      localStorage.setItem('jwt', idToken);
  
      //      (API Gateway + Lambda)
      const token = localStorage.getItem('jwt');
      if (!token) {
        setError('   .   .');
        return;
      }
      
      const res = await fetch(`${API_URL}/auth/user`, {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });
  
      if (res.ok) {
        const data = await res.json();
        console.log('  :', data);
      
        if (data && typeof data.user === 'object' && data.user !== null) {
          const safeUser = objectKeysToCamelCase(data.user);
          dispatch(successUserAuthentication(safeUser));
          navigate('/profile');
        } else {
          setError('   .');
        }
      } else {
        //     
        console.error(`   [${res.status}]`);
        let errorMsg = '  .';
        try {
          const errorData = await res.json();
          errorMsg = errorData.message || errorMsg;
        } catch (parseErr) {
          console.error('   :', parseErr);
        }
        setError(errorMsg);
      }
    } catch (err) {
      console.error('API  :', err);
      if (err.name === 'UserNotConfirmedException') {
        setError('  .');
      } else if (err.name === 'NotAuthorizedException') {
        setError('   .');
      } else {
        setError('   .');
      }
    }};

  return (
    <>
      <Logo />
      <LoginForm
        onChange={onChange}
        onSubmit={onSubmit}
        error={error}
        login={login}
      />
    </>
  );
};

export default Login;
