import React, { useEffect } from 'react';
import './SignupForm.scss';

const SignupForm = ({ onSubmit, onChange, register, error, onFileChange, isSignupComplete, code, setCode, handleConfirmCode, handleResendCode}) => {
  let form = null;

  useEffect(() => {
    if (form) {
      form.scrollIntoView({ block: 'start' });
    }
  }, [error]);

  return (
    <div className="signupForm form">
      {!isSignupComplete ? (
      <form onSubmit={onSubmit}
      ref={le => (form = le)}>
        <p className="error">{error}</p>
        <label htmlFor="mbit" required>
          MBTI
        </label>
        <select
          name="mbti"
          id="mbti"
          required
          value={register.mbti}
          onChange={onChange}
        >
          <option value="">mbti  .</option>
          <option value="intj">INTJ  </option>
          <option value="intp">INTP  </option>
          <option value="entj">ENTJ  </option>
          <option value="entp">ENTP    </option>
          <option value="infj">INFJ  </option>
          <option value="infp">INFP  </option>
          <option value="enfj">ENFJ  </option>
          <option value="enfp">ENFP  </option>
          <option value="istj">ISTJ  </option>
          <option value="isfj">ISFJ  </option>
          <option value="estj">ESTJ  </option>
          <option value="esfj">ESFJ  </option>
          <option value="istp">ISTP  </option>
          <option value="isfp">ISFP   </option>
          <option value="estp">ESTP   </option>
          <option value="esfp">ESFP   </option>
        </select>
        <p>
          mbti 
          <a href="https://www.16personalities.com/ko/%EB%AC%B4%EB%A3%8C-%EC%84%B1%EA%B2%A9-%EC%9C%A0%ED%98%95-%EA%B2%80%EC%82%AC">
            mbti 
          </a>
            .
        </p>
        <label htmlFor="email">email</label>
        <input
          type="email"
          name="email"
          required
          value={register.email}
          onChange={onChange}
          maxLength="100"
        />
        <label htmlFor="password">password</label>
        <input
          type="password"
          name="password"
          required
          value={register.password || ''}
          onChange={onChange}
          maxLength="18"
          minLength="6"
        />
        <label htmlFor="passwordConfirm">password confirm</label>
        <input
          type="password"
          name="passwordConfirm"
          required
          value={register.passwordConfirm || ''}
          onChange={onChange}
          maxLength="18"
          minLength="6"
        />
        <label htmlFor="name">name</label>
        <input
          type="text"
          name="name"
          required
          value={register.name || ''}
          onChange={onChange}
          maxLength="13"
        />
        <p className="title"></p>
        <div className="gender-area">
          <input
            type="radio"
            name="gender"
            value="female"
            id="female"
            checked={register.gender === 'female'}
            onChange={onChange}
            required
          />
          <label htmlFor="female"></label>
          <input
            type="radio"
            name="gender"
            value="male"
            id="male"
            checked={register.gender === 'male'}
            onChange={onChange}
          />
          <label htmlFor="male"></label>
        </div>
        <label htmlFor="file"> </label>
        <input
          type="file"
          name="file"
          id="file"
          onChange={onFileChange}
          accept="image/x-png,image/gif,image/jpeg"
          required
        />
        <label htmlFor="description"></label>
        <textarea
          type="text"
          name="description"
          maxLength="60"
          value={register.description}
          onChange={onChange}
        />
        <button>Sign up</button>
      </form>
      ) : (
        //     UI
        <div className="verify-section">
          <h3> </h3>
          <p>    .</p>
          <input
            type="text"
            value={code}
            onChange={(e) => setCode(e.target.value)}
            placeholder="  "
          />
          <button onClick={handleConfirmCode}> </button>
          <button onClick={handleResendCode}>  </button>
          {error && <p className="error">{error}</p>}
        </div>
      )}
    </div>
  );
};

export default SignupForm;
